  --brandSecondary: #294C88;
  --divider: #E5E5E5;
  --placeholder: #BABABA;
  --menu: #fafafa;
  --cardColor: #fff;
  --green: #00A6A4;
  --red: #ff0000;
  --slideCard: #F2FBFB;
  --icon-color: #999999;
}

html[data-theme='dark'] {
