.toggle-container {
	position: relative;
}

input[type="checkbox"].toggle  {
	position: absolute;
	opacity: 0;
}

input[type="checkbox"].toggle + div {
	vertical-align: middle;
	width: 40px;
	height: 20px;
	border-radius: 999px;
	background-color: var(--placeholder);
	-webkit-transition-duration: .4s;
	transition-duration: .4s;
	-webkit-transition-property: background-color, box-shadow;
	transition-property: background-color, box-shadow;
	cursor: pointer;
}

input[type="checkbox"].toggle + div span {
	position: absolute;
    font-size: 1.6rem;
    color: white;
    margin-top: 12px;
}

input[type="checkbox"].toggle + div span:nth-child( 1 ) {
    margin-left: 15px;
}

input[type="checkbox"].toggle + div span:nth-child( 2 ) {
    margin-left: 55px;
}

input[type="checkbox"].toggle:checked + div {
	width: 50px;
	background-position: 0 0;
}

input[type="checkbox"].toggle + div {
	width: 50px;
	height: 28px;
}


input[type="checkbox"].toggle:checked + div {
	background-color: var(--primary);
}

input[type="checkbox"].toggle + div > div {
	float: left;
	width: 20px; 
	height: 20px;
	border-radius: inherit;
	background: #ffffff;
	-webkit-transition-timing-function: cubic-bezier(1,0,0,1);
	transition-timing-function: cubic-bezier(1,0,0,1);
	-webkit-transition-duration: 0.4s;
	transition-duration: 0.4s;
	-webkit-transition-property: transform, background-color;
	transition-property: transform, background-color;
	pointer-events: none;
	margin-top: 4px;
	margin-left: 3px;
}

input[type="checkbox"].toggle:checked + div > div {
	-webkit-transform: translate3d(20px, 0, 0);
	transform: translate3d(20px, 0, 0);
	background-color: #ffffff;
}

input[type="checkbox"].bigtoggle.toggle + div > div {
	width: 44px;
	height: 44px;
	margin-top: 1px;
}


input[type="checkbox"].toggle:checked + div > div {
	-webkit-transform: translate3d(24px, 0, 0);
	transform: translate3d(24px, 0, 0);
}

